.small_sidebar {
  width: 72px;
  padding-right: 0;
  border-top: 1px solid $bg-nav-color;

  .small_sidebar_container {
    
    a {
      width: 100%;
      height: 100%;
      padding: 2rem;
      @include center;
      &:hover {
        background-color: $bg-color-hover;
      }

      .sidebar_icon_container {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        &:hover {
          background: none;
        }

        .sidebar_icon {
          margin-bottom: .75rem;
        }
      }
    }

    a.active {
      background-color: $bg-color-hover;
    }

  }
}